import Photo from '../assets/cvPhoto.jpg';
import pdfCV from '../assets/CV_ZarinRahmanMohima.pdf';
import { Fade, Slide } from "react-reveal";

const About = () => {
    return (
        <div class="aboutme" id="About">
            <Fade duration={1000}>
                <div>
                    <img class="about-pic" src={Photo} alt="Profile Photo" />

                </div>
            </Fade>
            <Slide left duration={1000}>
                <div>
                    <div class="about-des">
                        <h2 class="about-h2">About Me</h2>
                        <p class="about-p">My area of interest is full stack web development, AI, analytical problem solving and I aspire 
                        <br/> to gain more expertise in these fields. My strong devotion towards work, patience and adaptibility
                        <br/> according to dynamic environments have helped me develop a decent set of skills in these fields.

                </p>
                        <br />
                        <h2 class="about-h2">Contact Details</h2>
                        <p class="about-p">
                            Zarin Rahman Mohima
                    <br /> zarinrahmanmohima@gmail.com
                    <br /> +88 01733202988
                </p>
                        <a href={pdfCV} download="" class="pdf-download">
                            Download Resume
                    <i className="fa fa-download"></i> </a>
                    </div>
                </div>
            </Slide>
        </div>
    );
}
export default About;